@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Playfair+Display&display=swap);
html {
  background: url(/static/media/bar.c512ee19.jpg) no-repeat center center fixed;
  background-image: linear-gradient(
    0deg
    , rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/bar.c512ee19.jpg);
  background-size: cover;
  height: 100%;
  overflow: hidden;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-pair {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding-bottom: 10px;
  
}
img {
  max-width: 50%;
  height: auto
}

body {
  padding-top: 1%;
  padding-bottom: 1%;
  margin: 0;
}

/*****************************************************************************
Formating for Left Portion of Page (title and form)
******************************************************************************/

.left {
  top: 10%;
  position: relative;
  padding:3rem;
  display: -webkit-flex;
  display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
}

.left h1 {
  color: #f3e7f1!important;
  word-wrap: break-word;
  font-family: "Playfair Display",serif!important;
  text-align: left;
  font-size: 6rem;
  font-weight: 700;
}


.container {
  display: -webkit-flex;
  display: flex;
  border-radius: 10ex;
}




.left .form-container{
  background-color: rgba(153,98,65,.7);
  border-radius: 30px;
  padding: 10px;
  margin-top: 6.25%;
  margin-left: 25%;
  margin-right: 15%;
  position: relative;
  top: 0;
  bottom: 0;
}

.form-container form{
  text-align: center;
  padding: 10px;
}

/*****************************************************************************
Formating for Right Portion of Page Results
******************************************************************************/


.right {
  width: 90%;
  padding: 5rem 0 0 0;
  margin-right: 10px;
}

.right .results-container {
  display: block;
  margin: 2%;
  /* margin-top: 20%;  */
}

.CocktailList {
  color: #ffffff;
  background-color: rgba(48,28,19,.7);
  border-radius: 15px;
  padding: 10px;
  margin: 7% 5% 7% 5%;
  position: relative;
  overflow-y: scroll;
  max-height: 550px;
}


.cocktailList li {
  width: 100%;
padding-top: 5px;
padding-bottom: 5px;
}

.cocktailList ul{
  text-align: center;
  color: #ffffff;
  padding: 0px;
  margin: 0px;
}

ul {
  list-style-type: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

/*****************************************************************************
Formating for Cocktail Recipe View 
******************************************************************************/
.Cocktail {
  /* background-color: #f3e7f16f; */
  position: relative;
  max-height: 650px;

}

.Cocktail button {
  position: absolute;
  top: -2%;
  right: 0px;
  z-index: 2;
  border: none;
  background: transparent;
  color: black;
  cursor: pointer;
}

.Cocktail .button:hover {background-color: #d8fab5}



.Cocktail h2{
  color: #b85736 !important;
  font-family: "Playfair Display",serif!important;
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 1.2;
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
  -webkit-margin-after: 0.2em;
          margin-block-end: 0.2em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;

}

.Cocktail p {
  font-family: 'Cabin', sans-serif;
}

.Cocktail li{
  font-family: 'Cabin', sans-serif;
}

.Cocktail li:not(:last-child) {
  margin-bottom: 5px;
}

.Cocktail-layout {
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  display: -webkit-flex;
  display: flex;
  margin: 2%;
}

.Cocktail-layout .column {
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  display: -webkit-flex;
  display: flex;
  text-align: left;
}

.Cocktail-layout .col-item {
  -webkit-flex: 30 0 50%;
          flex: 30 0 50%;
  max-width: 100%;
  display: -webkit-flex;
  display: flex;
  color: #111111;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden
}

.Cocktail-layout .row {
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  min-height: 100%;
  max-width: 100%;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  display: -webkit-flex;
  display: flex;
  color: #111111;
}

.Cocktail-layout .row-item {
  /* text-align: center; */
  /* flex: 0 0 50%; */
  padding: 2%;
  max-width: 50%;
  background-color: transparent !important;
  /* opacity: 1; */
  /* position: relative; */
  /* display: flex; */
}

.Cocktail-layout .Cocktail_title {
  -webkit-justify-content: center;
          justify-content: center;
  height: auto;
  position: absolute;
  top: -12px;
  left: -5px;
  padding: 7px;
  z-index: 1;
  overflow:visible;
  background-color: #f3e7f1;
  border: 5px solid #f2fde6;
}

.Cocktail-layout .Cocktail_title .Cocktail_name {
  font-family: "Playfair Display",serif!important;
  color: #e4683d!important;
  font-size: 3rem;
  font-weight: 700;
  -webkit-margin-before: 0.03em;
          margin-block-start: 0.03em;
  -webkit-margin-after: 0.03em;
          margin-block-end: 0.03em;
}

.Cocktail-layout .Cocktail_title .Cocktail_rating {
  font-family: 'Cabin', sans-serif;
  font-size: 14px;
  color: #70757a;
}

.Cocktail-layout .text-box{
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  display: -webkit-flex;
  display: flex;
  padding: 3%;
  font-size:0.8rem;
  max-height: 300px;
  overflow-y: scroll;
}


.Cocktail-layout .text-box#Ingredients{
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #f3e7f1;
}

 .Cocktail-layout .text-box#Description{
  -webkit-flex: 2 1;
          flex: 2 1;
  background-color:#c6d4ce;
  /* background-color:#f2fde6;  */
 }
  

 .Cocktail-layout .Cocktail_image {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden; 
}

.Cocktail-layout .image-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.Cocktail-layout img.Cocktail_image {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  height: 350px;
}



/*****************************************************************************
End formatting for Cocktail Recipe View 
******************************************************************************/





.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

